import React from 'react'
import { IconButton, MenuItem, Menu, ClickAwayListener } from '@material-ui/core'
import MoreVertIcon from '@material-ui/icons/MoreVert'

class TooltipMenu extends React.Component {
    constructor(props) {
        super(props)
        this.state = {
            options: this.props.options,
            anchorEl: null,
            open: false
        }
    }

    handleClick = event => {
        this.setState({ anchorEl: event.currentTarget, open: true })
    }

    handleClose = clicked => {
        this.setState({ anchorEl: null, open: false })
        this.props.onSelect(clicked)
    }

    render() {
        const { anchorEl } = this.state

        return (
            <ClickAwayListener onClickAway={() => this.setState({ open: false })}>
                <div>
                    <IconButton
                        aria-label="More"
                        aria-owns={this.state.open ? 'long-menu' : null}
                        aria-haspopup="true"
                        onClick={this.handleClick}
                    >
                        <MoreVertIcon />
                    </IconButton>
                    <Menu
                        id="long-menu"
                        anchorEl={anchorEl}
                        open={this.state.open}
                        style={{ marginLeft: '50px' }}
                    >
                        {this.state.options.map((option, index) => (
                            <MenuItem key={index} onClick={() => this.handleClose(option.key)}>
                                {option.value}
                            </MenuItem>
                        ))}
                    </Menu>
                </div>
            </ClickAwayListener >
        )
    }
}

export default TooltipMenu