import React, { Component } from 'react'
import { BrowserRouter as Router, Route } from 'react-router-dom'
import Login from './login/Login'
import Home from './home/Home'
import Sidebar from './common/Sidebar'
import Topbar from './common/Topbar'

class Root extends Component {

    constructor(props) {
        super(props)
        this.state = {
            isLogged: true,
            showSidebar: true
        }
    }

    componentDidMount() {
        this.isLogged()
            .then(() => this.setState({ isLogged: true }))
            .catch(() => this.setState({ isLogged: false }))
    }

    isLogged() {
        return new Promise((resolve, reject) => {
            resolve(true)
        })
    }

    changeIsLogged(isLogged){
        this.setState({isLogged: isLogged})
    }

    toggleSidebar() {
        this.setState({showSidebar: !this.state.showSidebar})
    }

    render() {
        return this.state.isLogged ? (
            <div className='fullHeight' style={{ display: 'flex' }}>
                <Topbar sidebarOpen={this.state.showSidebar} toggleSidebar={() => this.toggleSidebar()}/>
                {this.state.showSidebar ? <Sidebar /> : null}
                <div className='contentContainer'>
                    <Home />
                </div>
            </div>
        ) : <Login  isLogged={isLogged => this.changeIsLogged(isLogged)}/>
    }
}

export default Root