import React, { Component } from 'react'
import MultiSelect from "@kenshooui/react-multi-select"
import { Button, TextField, Dialog, DialogActions, DialogContent, DialogContentText, DialogTitle } from '@material-ui/core'

class FormDialog extends Component {

    constructor(props) {
        super(props)
        let propsToState = {}
        this.props.fields.forEach(field => propsToState[field.name] = field.value)
        this.state = Object.assign({}, propsToState, { selectedItems: [] })
        console.log(this.props)
    }

    handleClose = () => {
        this.setState({ open: false })
        this.props.onClose()
    }

    onChange(event) {
        this.setState({ [event.target.name]: event.target.value })
    }

    handleChangeSelectItem(items){
        this.setState({selectedItems: items})
    }

    saveChanges() {
        this.props.onSave({ type: this.props.type, fields: this.state })
    }

    renderFields() {
        return this.props.fields.map((field, index) => <TextField
            key={index}
            autoFocus
            fullWidth={true}
            margin="dense"
            value={this.state[field.name]}
            name={field.name}
            label={field.label}
            type={field.type}
            onChange={event => this.onChange(event)}
        />)
    }

    render() {
        return (
            <div>
                <Dialog
                    open={this.props.open}
                    aria-labelledby="form-dialog-title"
                    className={this.props.className}
                    fullWidth={true}
                    maxWidth={this.props.maxWidth}
                >
                    <DialogTitle id="form-dialog-title">{this.props.dialogTitle}</DialogTitle>
                    <DialogContent>
                        {this.props.dialogText ? <DialogContentText>
                            {this.props.dialogText}
                        </DialogContentText> : null}
                        {this.renderFields()}
                        {this.props.multiSelect && <div className='modalBoxMultipleSelect'><MultiSelect
                            items={this.props.multiSelectItems}
                            selectedItems={this.state.selectedItems}
                            onChange={items => this.handleChangeSelectItem(items)}
                        /></div>}
                    </DialogContent>
                    <DialogActions>
                        <Button onClick={this.handleClose} color="primary">
                            Cancel
                        </Button>
                        <Button onClick={() => this.saveChanges()} color="primary">
                            {this.props.submitButtonText}
                        </Button>
                    </DialogActions>
                </Dialog>
            </div>
        )
    }
}

export default FormDialog