import React, { Component } from 'react'
import { List, ListItem, Collapse, ListItemText, ListItemIcon, Divider, ListSubheader, IconButton } from '@material-ui/core'
import { ExpandLess, ExpandMore, Send as SendIcon, AddCircle } from '@material-ui/icons'
import FormDialog from '../common/FormDialog'
import TooltipMenu from '../common/subComponents/TooltipMenu'
import * as CONSTANTS from '../../utils/constants'

const addEditGroupModalFields = [
    { name: 'test', label: 'Group name', type: 'text', value: '' }
]

const addEditCategoryModalFields = [
    { name: 'test', label: 'Category name', type: 'text', value: '' }
]

const toolTipOptionsGroup = [
    { value: 'Editare grupa', key: 'edit' },
    { value: 'Adaugare categorie', key: 'add' }
]

const toolTipOptionsElement = [
    { value: 'Editare categorie', key: 'edit' },
    { value: 'Adaugare element', key: 'add' }
]

const multipleSelectItems = [
    { id: 0, label: "item 1" },
    { id: 2, label: "item 2", disabled: true },
    { id: 3, label: "item 3", disabled: false },
    { id: 4, label: "item 4" }
]

let sidebarTree = [
    {
        edit: true, text: 'test3', key: '1', childrens: [
            {
                edit: true, text: 'test2', key: '11', childrens: [
                    { edit: true, text: 'test1', key: '111' },
                    { edit: true, text: 'test2', key: '112' }
                ]
            }
        ]
    },
    {
        edit: true, text: 'test3', key: '2', childrens: [
            {
                edit: true, text: 'test2', key: '21', childrens: [
                    { edit: true, text: 'test1', key: '211' },
                    { edit: true, text: 'test2', key: '212' }
                ]
            }
        ]
    }
]

const EXPAND = 'expand'
const SELECT = 'select'

class Sidebar extends Component {
    constructor(props) {
        super(props)
        this.state = {
            open: false,
            sidebarState: {},
            expandedAll: false,
            renderSidebar: false,
            openGroupDialog: false,
            groupDialogTitle: 'Create new Group',
            groupDialogType: CONSTANTS.CREATE,
            groupButtonText: 'send',
            openCategoryDialog: false,
            categoryDialogTitle: 'Create new Group',
            categoryDialogType: CONSTANTS.CREATE,
            categoryButtonText: 'send'
        }
    }

    componentWillMount() {
        let sidebarState = {}
        let recursiveStateBuild = tree => {
            tree.forEach(element => {
                sidebarState[element.key] = { selected: false, expanded: false }
                if (element.childrens) {
                    recursiveStateBuild(element.childrens)
                }
            })
        }
        recursiveStateBuild(sidebarTree)
        this.setState({ sidebarState: sidebarState, renderSidebar: true })
    }

    /* group */
    saveGroup(values) {
        console.log(values)
    }

    addGroup() {
        //Add group
        this.setState({
            groupDialogType: CONSTANTS.CREATE,
            openGroupDialog: true,
            groupDialogTitle: 'Create group',
            groupButtonText: 'create',
            enableMultiSelect: false
        })
    }

    toolTipGroupClickHandler(action, id) {
        console.log(action, id)
        if (action === CONSTANTS.EDIT) { //edit group
            this.setState({
                groupDialogType: CONSTANTS.EDIT,
                openGroupDialog: true,
                groupDialogTitle: 'Edit group #' + id,
                groupButtonText: 'edit'
            })
        } else { //add category
            this.setState({
                categoryDialogType: CONSTANTS.EDIT,
                openCategoryDialog: true,
                categoryDialogTitle: 'Add category #' + id,
                categoryButtonText: 'create'
            })
        }
    }

    /* category */
    saveCategory(values) {
        console.log(values)
    }

    toolTipCategoryClickHandler(action, id) {
        if (action === CONSTANTS.EDIT) {
            this.setState({
                categoryDialogType: CONSTANTS.EDIT,
                openCategoryDialog: true,
                categoryDialogTitle: 'Edit category #' + id,
                categoryButtonText: 'create'
            })
        }
    }

    handleClick(key, action /*expand or select */) {
        let sidebarStateCopy = Object.assign({}, this.state.sidebarState)
        if (action === SELECT) // reset all selected to false
            Object.keys(sidebarStateCopy).forEach(element => {
                if (sidebarStateCopy[element].selected)
                    sidebarStateCopy[element] = { selected: false, expanded: sidebarStateCopy[element].expanded }
            })
        sidebarStateCopy[key][action === EXPAND ? 'expanded' : 'selected'] = !sidebarStateCopy[key][action === EXPAND ? 'expanded' : 'selected']
        this.setState({ sidebarState: sidebarStateCopy })
    }

    toggleExpandAll() {
        let sidebarStateCopy = Object.assign({}, this.state.sidebarState)
        Object.keys(sidebarStateCopy).forEach(element => {
            sidebarStateCopy[element] = { selected: sidebarStateCopy[element].selected, expanded: !this.state.expandedAll }
        })
        this.setState({ sidebarState: sidebarStateCopy, expandedAll: !this.state.expandedAll })
    }

    sidebarTree(tree, nested = false, depth = 0) {
        return tree.map((element, index) => {
            if (element.childrens) {
                return (<div>
                    <ListItem disableGutters={true} divider={true} key={element.key} id={element.key} button >
                        <ListItemIcon onClick={() => this.handleClick(element.key, EXPAND)}>
                            {this.state.sidebarState[element.key].expanded ?
                                <ExpandLess /> :
                                <ExpandMore />
                            }
                        </ListItemIcon>
                        <ListItemText onClick={() => this.handleClick(element.key, EXPAND)} inset primary={element.text} />
                        {element.edit ? <TooltipMenu
                            actionId={element.key}
                            onSelect={action => nested ? this.toolTipCategoryClickHandler(action, element.key) : this.toolTipGroupClickHandler(action, element.key)}
                            options={nested ? toolTipOptionsElement : toolTipOptionsGroup} />
                            : null}
                    </ListItem>
                    <Collapse in={this.state.sidebarState[element.key].expanded} timeout="auto" unmountOnExit>
                        <List component="div" disablePadding>
                            {this.sidebarTree(element.childrens, true, ++depth)}
                        </List>
                    </Collapse>
                </div>)
            } else {
                return <ListItem disableGutters={true} dense={true} divider={true} key={element.key} onClick={() => this.handleClick(element.key, SELECT)}
                    button
                    selected={this.state.sidebarState[element.key].selected ? true : false}>
                    <ListItemIcon>
                        <SendIcon />
                    </ListItemIcon>
                    <ListItemText inset primary={element.text} />
                </ListItem>
            }
        })
    }

    render() {
        return this.state.renderSidebar ? (
            <div className='sidebar'>
                <FormDialog
                    fields={addEditGroupModalFields}
                    open={this.state.openGroupDialog}
                    submitButtonText={this.state.groupButtonText}
                    type={this.state.groupDialogType}
                    onSave={values => this.saveGroup(values)}
                    onClose={() => this.setState({ openGroupDialog: false })}
                    dialogTitle={this.state.groupDialogTitle}
                    maxWidth={'xs'}
                />
                <FormDialog
                    fields={addEditCategoryModalFields}
                    open={this.state.openCategoryDialog}
                    submitButtonText={this.state.categoryButtonText}
                    type={this.state.categoryDialogType}
                    onSave={values => this.saveCategory(values)}
                    onClose={() => this.setState({ openCategoryDialog: false })}
                    multiSelect={true}
                    multiSelectItems={multipleSelectItems}
                    dialogTitle={this.state.categoryDialogTitle}
                    maxWidth={'md'}
                />
                <List component="nav"
                    subheader={<ListSubheader component="div" className='sidebarSubheader'>
                        {this.state.expandedAll ?
                            <IconButton color="secondary" onClick={() => this.toggleExpandAll()} ><ExpandMore /> </IconButton> :
                            <IconButton color="secondary" onClick={() => this.toggleExpandAll()}><ExpandLess /> </IconButton>
                        }
                        <IconButton color="secondary">
                            <AddCircle onClick={() => this.addGroup()} />
                        </IconButton>
                    </ListSubheader>}
                >
                    <Divider />
                    {this.sidebarTree(sidebarTree)}
                </List>
            </div>
        ) : null
    }
}

export default Sidebar