import React, { Component } from 'react'
import { TextField, Button, Checkbox, FormControlLabel, CircularProgress, IconButton } from '@material-ui/core'
import { Lock } from '@material-ui/icons'

class Login extends Component {
    constructor(props) {
        super(props)
        this.state = {
            isLoading: false
        }
    }

    login() {
        this.setState({
            isLoading: true
        })
        setTimeout(() => {
            this.setState({
                isLoading: false
            })
            this.props.isLogged(true)
        }, 2000)
    }

    render() {
        return (
            <div className='loginContainer'>
                <div className='login'>
                    <Lock fontSize='large' color='primary' />
                    <TextField
                        className='loginInput'
                        label='Username'
                    />
                    <TextField
                        className='loginInput'
                        label='Password'
                        type='password'
                    />
                    <div className='loginRememberMe'>
                        <FormControlLabel
                            className='loginRememberMeLabel'
                            style={{ color: '#dedede' }}
                            control={
                                <Checkbox
                                    value='checkedF'
                                    color='primary'
                                />
                            }
                            label='Remember me'
                        />
                    </div>
                    <Button className='loginButton' variant="contained" onClick={() => this.login()} color="primary">
                        {this.state.isLoading ? <CircularProgress style={{ color: '#ffffff' }} size={24} /> : 'Login'}
                    </Button>

                </div>
            </div>
        )
    }
}

export default Login
